module.exports = "<section><h1 style=\"font-size:3.42em;\">Create React App</h1><p>React bootstrapping starter app from Facebook</p><p><a href=\"https://github.com/facebookincubator/create-react-app\">github create-react-app</a></p></section><section><ul><li>Jumpstart React app with no configuration</li><li>Opinionated setup of Webpack and Babel<li>React, JSX, ES6, CSS, Jest Testing</li></li><li>Allows for easy gradual adoption</li><li>`Eject` to export custom configuration</li></ul></section><section><h1>Webpack</h1><p>webpack is a module bundler for modern JavaScript applications. And more.</p><p><a href=\"https://webpack.js.org/\">webpack.js.org</a></p></section><section><ul><li>Webpack bundles up all of your dependencies (to oversimplify it)</li><li>Produces static assets for what you need</li><li>ES6, Linting, CSS processing, SourceMaps!</li><li>`create-react-app` handles the Webpack config for you</li></ul></section>";

