module.exports = "<section><h1>Hapi</h1><p>Framework for building REST API services</p><p><a href=\"http://hapijs.com/\">hapijs.com</a></p></section><section><ul><li>A rich framework for building applications and services</li><li>Created by smart people at Walmart Labs</li><li>Focuses on configuration</li><li>Provides us with a complete backend for our lab project</li></ul></section><section><h1>Jest</h1><p>Zero config, parallelized, snapshot testing framework for JS & React</p><p><a href=\"https://facebook.github.io/jest/\">facebook.github.io/jest</a></p></section><section><ul><li>Fast. Run only for related changes. Run in parallel.</li><li>Built in assertion matchers, mocking library and code coverage</li><li>Snapshots for better React component verifications</li><li>Works with TypeScript, Angular, Vue.js</li></ul></section><section><p>BDD-style (Behavior Driven Development)</p><pre style=\"width:100%;\"><code class=\"javascript\" data-trim style=\"overflow:visible;\">\ndescribe('when there is no message', function () {\n  beforeEach(function () {\n    Snackbar = require('./snackbar');\n    element = TestUtils.renderIntoDocument(&lt;Snackbar /&gt;);\n  });\n\n  it('should hide the snackbar', function () {\n    var div = TestUtils.findRenderedDOMComponentWithTag(element, 'div');\n    expect(div.props.className).contains('hide');\n  });\n});\n\n</code>\n</pre></section><section><h1>axios / moxios</h1><ul><li>axios is a promise based HTTP client for the browser and node.js</li><li>moxios is for mocking axios requests while testing</li></ul></section><section><p>Stubbing a request</p><pre><code class=\"javascript\" data-trim>\n    moxios.stubRequest('/api/users', {\n      status: 200,\n      response: [\n        { id: 1, firstName: 'Fred', lastName: 'Flintstone' },\n        { id: 2, firstName: 'Wilma', lastName: 'Flintstone' }\n      ]\n    });\n\n</code>\n</pre></section><section><h1>Project Layout</h1></section><section><ul><li>In this training class, you'll be building a React application that tracks employee timesheets across different projects</li><li>To help speed things along so we can focus on the really important parts (like React/Redux), we've created the base project structure for you</li></ul></section><section><p>Let's take a look</p></section><section><h1>Lab 01</h1><h2 class=\"plain\">Application Setup</h2></section>";

