module.exports = "<section><h1>React</h1><p><a href=\"http://facebook.github.io/react/index.html\">website</a></p></section><section><h3>A JavaScript library for building user interfaces</h3></section><section><h1>Virtual DOM</h1></section><section><ul><li>Replica of the Browser DOM</li><li>React redraws the entire screen on change</li><li>React calculates the minimum amount of changes required</li></ul></section><section><p><a href=\"http://facebook.github.io/react/docs/glossary.html\">More info?</a></p></section><section><p>For a bare-bones React component, we need to do 2 things</p><ul><li>Extend the react Component</li><li>Implement render()</li></ul></section><section><h2 class=\"lower\">Extend React Component</h2></section><section><ul><li>Defines a component class</li><li>A component implements a render method which returns a single child</li><li>That child may have an arbitrarily deep child structure</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nimport React, { Component } from 'react';\nclass Hello extends Component {\n  render() {...}\n}\n\nmodule.exports = Hello;\n</code>\n</pre></section><section><h2>Component Specifications</h2></section><section><h2 class=\"lower\">render()</h2><ul><li>Required</li><li>Returns a single child component that is either a virtual representation of a native DOM component or composite component</li></ul></section><section><h2 class=\"lower\">render()</h2><ul><li>You should return null (or false) to indicate that you don't want anything rendered</li><li>React renders a &lt;noscript&gt; tag to work with our current diffing algorithm</li><li>When returning null or false, React.findDOMNode(this) will return null</li></ul></section><section><h2 class=\"lower\">render()</h2><ul><li>Should be pure</li><li>Should not modify component state</li><li>Should return the same result each time it's invoked</li><li>Should not read from or write to the DOM</li><li>Work should be performed in componentDidMount() or the other lifecycle methods</li></ul></section><section><h2 class=\"lower\">constructor</h2><ul><li>Invoked at object creation</li><li>Receives \"props\"</li><li>This is a great place to setup your initial state</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nconstructor(props) {\n  super(props);\n  this.state = {\n    saveText: 'Update',\n    employee: {},\n    errors: {}\n  };\n}\n</code>\n</pre></section><section><h2 class=\"lower\">defaultProps</h2><ul><li>Values in the mapping will be set on this.props if that prop is not specified by the parent component</li><li>This invoked before any instances are created and therefore it can't rely on this.props</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nclass Foo extends React.Component {\n    render() {\n        return (&lt;div>{this.props.foo}&lt;/div>)\n    }\n}\nFoo.defaultProps = {\n    foo: ‘Foo’\n}\n</code>\n</pre></section><section><h2 class=\"lower\">propTypes: {}</h2><ul><li>The propTypes object allows you to validate props being passed to your components</li><li>Defined in a similar matter to defaultProps</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nimport PropTypes from ‘prop-types’\n...\nFoo.propTypes: {\n   max:        PropTypes.number.isRequired,\n   maxVisible: PropTypes.number,\n   onChange:   PropTypes.func.isRequired\n}\n\n</code>\n</pre></section><section><h1>JSX</h1></section><section><ul><li>JavaScript syntax extension that looks similar to XML</li><li>Compiles to actual JavaScript that you'll be able to see during labs</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nrender() {\n  return (\n    &lt;div className=\"ui message\"&gt;\n      &lt;div className=\"ui huge header\"&gt;{this.state.greeting}&lt;/div&gt;\n      &lt;div className=\"ui large header\"&gt;{this.props.friend}&lt;/div&gt;\n    &lt;/div&gt;\n  );\n}\n\n</code>\n</pre></section><section><p>React's JSX uses the upper vs. lower case convention to distinguish between local component classes and HTML tags</p><ul><li>UpperCase = Component</li><li>lowercase = tag</li></ul></section><section><p>Since JSX is a superset of JavaScript, identifiers like class and for as XML attribute names is discouraged</p><ul><li>htmlFor = for</li><li>className = class</li></ul></section><section><h2>Data: State vs. Props</h2></section><section><ul><li>\"props\" are the data you pass into your component via attributes</li><li>Used for application data that you need to display or manage with your component</li></ul></section><section><ul><li>\"state\" is your component's internally scoped ... state</li><li>Used for the behavior of your component: open, disabled, submitted, etc.</li></ul></section><section><ul><li>One exception: \"Controller\" components</li><li>They will have a state that represents the application data</li><li>They pass their state as props to their children components</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nrender() {\n  return (\n    &lt;EmployeeForm employee={this.state.employee}\n      errors={this.state.errors}\n      validateAll={this.validateAll}\n      hasErrors={this.hasErrors}\n      saveText={this.state.saveText}\n      onSave={this.saveEmployee}\n      validate={this.validate}\n      toggleAdmin={this.toggleAdmin} /&gt;\n  );\n}\n\n</code>\n</pre></section><section><h2>Component Lifecycle</h2><ul><li>Mounting</li><li>Updating</li><li>Unmounting</li></ul></section><section><h2>Mounting</h2><p>These methods are called when an instance of a component is being created and inserted into the DOM:</p><ol><li>constructor()</li><li>componentWillMount()</li><li>render()</li><li>componentDidMount()</li></ol></section><section><h3 class=\"lower\">componentWillMount()</h3><ul><li>Invoked immediately before mounting occurs -- before render()</li><li>If you call setState within this method, render() will see the updated state</li><li>Since its before render, setState won't cause a \"re-render\"</li></ul></section><section><h3 class=\"lower\">render()</h3></section><section><h3 class=\"lower\">componentDidMount()</h3><ul><li>Invoked immediately after the initial rendering occurs</li><li>Setting state will cause \"re-render\"</li><li>Place to integrate with other JS frameworks</li><li>Place to Send AJAX requests</li></ul></section><section><h2>Updating:</h2><p>An update can be caused by changes to props or state.</p><ol><li>componentWillReceiveProps()</li><li>shouldComponentUpdate()</li><li>componentWillUpdate()</li><li>render()</li><li>componentDidUpdate()</li></ol></section><section><h4 class=\"lower\">componentWillReceiveProps(object nextProps)</h4><ul><li>Invoked when a component is receiving new props</li><li>This method is not called for the initial render</li></ul></section><section><h4 class=\"lower\">componentWillReceiveProps(object nextProps)</h4><ul><li>Used to react to a prop transition</li><li>This happens before render() is called when updating the state using this.setState()</li><li>The old props can be accessed via this.props</li><li>Calling this.setState() within this function will not trigger an additional render</li></ul></section><section><h4 class=\"lower\">shouldComponentUpdate(object nextProps, object nextState)</h4><ul><li>Invoked before rendering when new props or state are being received</li><li>This method is not called for the initial render or when forceUpdate is used</li></ul></section><section><h4 class=\"lower\">shouldComponentUpdate(object nextProps, object nextState)</h4><ul><li>Return false when you're certain that the transition to the new props and state will not require a component update</li><li>render() will be completely skipped until the next state change</li><li>componentWillUpdate() and componentDidUpdate() will not be called</li></ul></section><section><h4 class=\"lower\">componentWillUpdate(object nextProps, object nextState)</h4><ul><li>Invoked immediately before rendering when new props or state are being received</li><li>Not called for the initial render</li></ul></section><section><h4 class=\"lower\">componentWillUpdate(object nextProps, object nextState)</h4><ul><li>Cannot use this.setState() in this method</li><li>If you need to update state in response to a prop change, use componentWillReceiveProps instead</li></ul></section><section><h4 class=\"lower\">render()</h4></section><section><h4 class=\"lower\">componentDidUpdate(object prevProps, object prevState)</h4><ul><li>Invoked immediately after the component's updates are flushed to the DOM</li><li>Not called for the initial render</li><li>Setting state will cause \"re-render\"</li><li>Operate on the DOM when the component has been updated</li></ul></section><section><h2 class=\"lower\">Unmounting:</h2><h3 class=\"lower\">componentWillUnmount()</h3><ul><li>Invoked immediately before a component is unmounted from the DOM</li><li>Perform any necessary cleanup in this method</li></ul><ul><li>Invalidating timers</li><li>Cleaning up any DOM elements that were created in componentDidMount()</li></ul></section><section><h2 class=\"lower\">ReactTestUtils</h2><ul><li>Utilities to make it easy to test React components</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nimport ReactTestUtils from 'react-dom/test-utils';\n</code>\n</pre></section><section><h4 class=\"lower\">Simulate.{eventName}(element, [eventData])</h4><ul><li>Simulate an event dispatch on a DOM node with optional event data</li><li>Possibly the single most useful utility in ReactTestUtils</li><li>Has a method for every event that React understands</li></ul></section><section><pre><code class=\"javascript\" data-trim>\n// &lt;button ref=\"button\">...&lt;/button>\nconst node = this.refs.button;\nReactTestUtils.Simulate.click(node);\n</code>\n</pre></section><section><h4 class=\"lower\">renderIntoDocument(ReactComponent instance)</h4><ul><li>Render a component into a detached DOM node in the document</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nelement = ReactTestUtils.renderIntoDocument(\n  &lt;EmployeeForm employee={employee}\n    errors={errors}\n    validate={spies.validate}\n    hasErrors={spies.hasErrors}\n    toggleAdmin={spies.toggleAdmin}\n    onSave={spies.onSave} /&gt;\n);\n\n</code>\n</pre></section><section><h2>Expectation Helpers</h2></section><section><h4 class=\"lower\">isElement(ReactElement element)</h4><ul><li>Returns true if the element is any ReactElement</li></ul></section><section><h4 class=\"lower\">isElementOfType(ReactElement element, function componentClass)</h4><ul><li>Returns true if the element is a ReactElement that has a type of a React componentClass</li></ul></section><section><h4 class=\"lower\">isDOMComponent(ReactComponent instance)</h4><ul><li>Returns true if the instance is a DOM component (such as a &lt;div&gt; or &lt;span&gt;)</li></ul></section><section><h4 class=\"lower\">isCompositeComponent(ReactComponent instance)</h4><ul><li>Returns true if the instance is a composite component (created with React.createClass())</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nit('should instantiate the EmployeeForm', function () {\n  expect(ReactTestUtils.isCompositeComponent(element)).toBe(true);\n});\n\n</code>\n</pre></section><section><h4 class=\"lower\">isCompositeComponentWithType(ReactComponent instance, function componentClass)</h4><ul><li>Returns true if the instance is a composite component (created with React.createClass()) whose type is of a React componentClass</li></ul></section><section><h2>DOM Query Methods</h2></section><section><h4 class=\"lower\">scryRenderedDOMComponentsWithClass(ReactComponent tree, string className)</h4><ul><li>Finds all instances of components in the rendered tree that are DOM components and whose class matches className</li></ul></section><section><h4 class=\"lower\">findRenderedDOMComponentWithClass(ReactComponent tree, string className)</h4><ul><li>Like scryRenderedDOMComponentsWithClass() but expects there to be one result</li><li>Throws an exception if there is more than one match or no match at all</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nvar cancel = ReactTestUtils.findRenderedComponentWithClass(\n  element,\n  'cancel-btn'\n);\n\n</code>\n</pre></section><section><h4 class=\"lower\">scryRenderedComponentsWithType(ReactComponent tree, function componentClass)</h4><ul><li>Finds all instances of components with type equal to componentClass</li></ul></section><section><h4 class=\"lower\">findRenderedComponentWithType(ReactComponent tree, function componentClass)</h4><ul><li>Same as scryRenderedComponentsWithType() but expects there to be exactly one result</li><li>Throws an exception if there is more than one match or no match at all</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nvar cancel = ReactTestUtils.findRenderedComponentWithType(\n  element,\n  CancelButton\n);\n\n</code>\n</pre></section><section><h4 class=\"lower\">scryRenderedDOMComponentsWithTag(ReactComponent tree, string tagName)</h4><ul><li>Finds all instances of components in the rendered tree that are DOM components and whose tag name matches tagName</li></ul></section><section><h4 class=\"lower\">findRenderedDOMComponentWithTag(ReactComponent tree, string tagName)</h4><ul><li>Like scryRenderedDOMComponentsWithTag() but expects there to be exactly one result</li><li>Throws an exception if there is more than one match or no match at all</li></ul></section><section><pre><code class=\"javascript\" data-trim>\nvar button = ReactTestUtils.findRenderedDOMComponentWithTag(\n  cancel,\n  'button'\n);\n\n</code>\n</pre></section><section><h2>react-test-renderer</h2><p>Renderers for testing components without a DOM</p><p><a href=\"https://www.npmjs.com/package/react-test-renderer\">npm react-test-renderer</a></p></section><section><h4 class=\"plain\"> ReactTestRenderer</h4><p>Renders components to plain JS objects for testing</p><pre><code class=\"javascript\" data-no-escape>\n  const component = ReactTestRenderer.create(\n    &lt;Link page=\"https://xkcd.com/\"&gt;xkcd&lt;Link&gt;\n  );\n\n  expect(component.props.href).to.equal('https://xkcd.com/');\n  expect(component.children[0]).to.equal('xkcd');\n\n</code>\n</pre></section><section><h4 class=\"plain\"> ReactShallowRenderer</h4><p>Renders components to plain JS objects for testing w/o children</p><pre><code class=\"javascript\" data-no-escape>\n  const renderer = new ReactShallowRenderer()\n  const output = renderer.render( &lt;App/&gt; ).getRenderOutput();\n\n  let stringVal = JSON.stringify(output);\n  expect(stringVal).toMatch(/employees\\/:user_id\\/timesheets/);\n\n</code>\n</pre></section><section><h1>Lab 02</h1><h2>Your First React Component</h2></section>";

