module.exports = "<section><h1>React Forms</h1></section><section><ul><li>&lt;input&gt;, &lt;textarea&gt;, and &lt;option&gt; differ from other native components since they are mutated via user interactions</li><li>They provide interfaces to manage forms in response to user interactions</li></ul></section><section><h2>Interactive Props</h2></section><section><p>Props affected via user interactions:</p><ul><li>value:     supported by input and textarea</li><li>checked:   supported checkbox or radio</li><li>selected:  supported by option</li></ul></section><section><ul><li>Form components allow listening for changes</li><li>Set a callback to the onChange prop</li><li>The callback fires when:<ul><li>The value changes</li><li>Checked state changes</li><li>Selected state changes</li></ul></li></ul></section><section><h2>Controlled Components</h2></section><section><ul><li>Any &lt;input&gt; with value set is a controlled component</li><li>Value of the rendered element will always reflect the value prop</li></ul></section><section><h2>Uncontrolled Components</h2></section><section><ul><li>Any &lt;input&gt; that does not supply a value (or sets it to null)</li><li>Value of the rendered element will reflect the user's input</li></ul></section><section><h2>Validation</h2></section><section><ul><li>We can now use controlled components in our form to enforce validation</li></ul></section><section><pre><code class=\"javascript\" data-trim>\n(&lt;form name=\"employeeForm\"&gt;\n  &lt;input name=\"username\"\n    value={this.props.employee.username}\n    onChange={this.props.validate} /&gt;\n&lt;/form&gt;)\n\n</code>\n</pre></section><section><pre><code class=\"javascript\" data-trim>\nvalidate: function (event) {\n  var field = event.target.name;\n  var value = event.target.value;\n\n  // min length 1\n  if (!value || value.length &lt; 1) {\n    this.state.errors[field] =\n      'You must provide a username.';\n  }\n  // max length 40\n  else if (value.length &gt; 40) {\n    this.state.errors[field] =\n      'Username can only be 40 characters long.';\n  }\n\n  this.props.employee[field] = value;\n  return this.setState({\n    employee: this.state.employee,\n    errors: this.state.errors\n  });\n}</code>\n</pre></section><section><h1>lab prep</h1><p>LoginStore and LoginActions</p></section><section><h1>Lab 5</h1><h2>React Form Validation</h2></section>";

